import React from "react"
import { StaticQuery, graphql } from 'gatsby'

const TitleAndDescription = ({ data }) => {
  const { title, description } = data.site.siteMetadata

  return (
    <>
      <h2>{title}</h2>
      <p>{description}</p>
    </>
  ) 
}

const Header = () => (
  <StaticQuery 
    query={graphql`
      query {
        site {
          siteMetadata {
            title
            description
          }
        }
      }
    `}

    render={data => <TitleAndDescription  data={data}/>}
  />
)

export default Header
